@import 'https://fonts.googleapis.com/css?family=Open+Sans';

* {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

body {
/*	font-family: 'Open Sans', sans-serif;*/
  font: 14px "Century Gothic", Futura, sans-serif;
	line-height: 1.75em;
	font-size: 16px;
	background-color: #333;
	color: #aaa;
  margin: 20px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

a[href] {
	color: #1e8ad6;
}

a[href]:hover {
	color: #3ba0e6;
}

aside,
[class *= "sidebar"],
[id *= "sidebar"] {
	max-width: 90%;
	margin: 0 auto;
	border: 1px solid lightgrey;
	padding: 5px 15px;
}

ol, ul {
  padding-left: 30px;
  padding-right: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.square {
  background: #111;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
  outline: none;
}

.button {
  background: #111;
  border: 1px solid #999;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  text-align: center;
  width: 200px;
  margin-top: 80px;
  margin: 0 auto;
}

.active {
  background: gold;
}
.inactive {
  background: darkgray;
}
.final {
	background: green;
}
.final.active {
	background: darkgreen;
}
.number{
	color: gold;
}
.error {
  background: darkred;
}

.game {
  display: flex;
  flex-direction: column;
  justify-content: center;
  -webkit-transform: rotate(0deg);
}
.hints {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.hint {
	float: left;
}
.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #111;
  z-index: 3;
  cursor: pointer;
}

.field {
  margin: 0 auto;
  margin-top: 20px;
}
.rotate0{
  -ms-transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}
.rotate90{
  -ms-transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  text-decoration: underline;
}
.rotate180{
  -ms-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  text-decoration: underline;
}
.rotate270{
  -ms-transform: rotate(270deg);
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
  text-decoration: underline;
}
